	module.exports = {"data":{"post":{"html":"<p>Christopher's chicken pox seems to be winding down. The fever has been gone for 24 hours, and the blisters are starting to dry up. It looks like his infliction was milder than normal (maybe because of good nursing from RN Grandma). And he might be caught up with his sleep again. And he's not contagious anymore, so we might be able to arrange that visit.</p>","fields":{"title":"Pox Be Gone","date":"23 Apr 2002","url":"https://papascott.de/2002-04-23-pox-be-gone/"}}},"pathContext":{"slug":"/2002-04-23-pox-be-gone/","prev":{"fields":{"title":"Chicken Pox","date":"21 Apr 2002","slug":"/2002-04-21-chicken-pox/"}},"next":{"fields":{"title":"Update From The Desert","date":"02 May 2002","slug":"/2002-05-02-update-from-the-desert/"}}}}

